@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css";
body {
  background: #eee;
  margin: 0;
  padding: 50px 0 0;
  font-family: Roboto, sans-serif;
  position: relative;
}

.link-home {
  margin: 50px 0 0;
}

.todo-list .header {
  width: 100%;
  height: 80px;
  z-index: 5;
  background: #25b99a;
  border-radius: 0 0 10px 10px;
  padding: 1em;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 4px rgba(50, 50, 50, .3);
}

.todo-list .header .header__input {
  width: 100%;
  height: 100%;
  color: #fff;
  background: rgba(255, 255, 255, .2);
  border: none;
  border-radius: 5px 25px 25px 5px;
  outline: none;
  padding: .7em 1.2em;
  font-size: 1.2rem;
}

.todo-list .header .header__button {
  width: 50px;
  height: 50px;
  z-index: 2;
  text-align: center;
  color: #25b99a;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 25px;
  font-size: 1.3rem;
  position: absolute;
  top: 15px;
  right: 15px;
}

.todo-list main {
  background: #eee;
}

.todo-list hr {
  width: 50%;
  color: rgba(50, 50, 50, .2);
}

.todo-list .list {
  margin: 0;
  padding: 1em;
}

.todo-list .list .list__item {
  width: 100%;
  background: #fff;
  border-radius: 3px;
  justify-content: space-between;
  margin: 1em 0;
  padding: 1em;
  font-size: 1.3em;
  list-style: none;
  display: flex;
  box-shadow: 0 1px 2px rgba(50, 50, 50, .2);
}

.todo-list .list .list__buttons {
  font-size: 1.3rem;
}

.todo-list .list .list__button {
  text-align: center;
  width: 50px;
  cursor: pointer;
  background: #fff;
  border: none;
  font-size: 1.4rem;
}

.todo-list .list .fa-trash-o {
  color: rgba(50, 50, 50, .5);
}

.todo-list .list .list__bar {
  color: rgba(50, 50, 50, .3);
  font-size: 1.5rem;
}

.todo-list .list.current-list .fa-check-circle-o {
  color: #25b99a;
}

.todo-list .list.done-list .list__item {
  opacity: .6;
}

.todo-list .list.done-list .list__item .list__item-name {
  text-decoration: line-through;
}

.todo-list .list.done-list .fa-check-circle {
  color: #25b99a;
}

